import React from 'react';
import { Link } from 'gatsby';
import styled from 'styled-components';
import { Section, Container } from '@styles/global';
import ExternalLink from '@utils/externalLink';
import GithubIcon from '@static/icons/github.svg';
import { ReactComponent as Logo } from '@static/nfront-icon.svg';

const SOCIAL = [
    {
        icon: GithubIcon,
        link: '/',
    },
];

const FooterWrapper = styled.footer`
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: var(--alt-color);
    background-color: var(--footer-color);

    a {
        color: var(--yellow);
    }
`;

const StyledContainer = styled(Container)`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem 1.5rem;

    @media (min-width: ${props => props.theme.screen.sm}) {
        flex-direction: row;
        justify-content: space-between;
    }

    ${props =>
        props.fluid &&
        `
        max-width: 100%;
    `};
`;

const SocialIcons = styled.div`
    display: flex;

    img {
        margin: 0 8px;
        width: 24px;
        height: 24px;
    }

    @media (max-width: ${props => props.theme.screen.sm}) {
        margin-top: 30px;
    }
`;

const Copyright = styled.div``;

const GRID = styled(Container)`
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @media (min-width: ${props => props.theme.screen.sm}) {
        flex-direction: row;
    }
`;

const StyledSection = styled(Section)`
    background: #212529;
    padding: 3rem 0 !important;

    p {
        color: #6c757d;
    }

    .label {
        font-size: 14px;
        color: var(--accent-color);
    }

    .button {
        font-size: 14px;
    }
    ul {
        list-style-type: none;
        margin-left: 0;

        li {
            margin-bottom: 5px;
        }
    }

    svg {
        width: 50px;
        fill: var(--yellow);
    }
`;
const Footer = props => (
    <FooterWrapper {...props}>
        <StyledSection>
            <GRID>
                <div>
                    <p className="label">Headquarters</p>
                    <p>Tollbugata 24, 0157Oslo, Norway</p>
                    <p>
                        Los Angeles, CA
                        <br />
                        Warsaw, Poland
                    </p>
                </div>
                <div>
                    <p className="label">Services</p>
                    <ul>
                        <li>Media</li>
                        <li>FAQs</li>
                    </ul>
                </div>
                <div>
                    <p className="label">Legal</p>
                    <ul>
                        <li>Privacy</li>
                        <li>Terms and Conditions</li>
                        <li>GDPR</li>
                        <li>Disclaimer</li>
                    </ul>
                </div>
                <div>
                    <Logo />
                </div>
            </GRID>
        </StyledSection>
        <StyledContainer>
            <Copyright>
                &copy; {new Date().getFullYear()}. Built for{' '}
                <Link to="/">nFront</Link> by{' '}
                <ExternalLink href="https://www.adamidea.com/">
                    ADAMIDEA
                </ExternalLink>
            </Copyright>
            <SocialIcons>
                {SOCIAL.map(({ icon, link }) => (
                    <ExternalLink key={icon} href={link}>
                        <img src={icon} alt="link" />
                    </ExternalLink>
                ))}
            </SocialIcons>
        </StyledContainer>
    </FooterWrapper>
);

export default Footer;
