import React, { Component } from 'react';
import { Link, graphql } from 'gatsby';
import styled from 'styled-components';
import Layout from '@common/layout';
import Header from '@common/header';
import Footer from '@common/footer';
import Menu from '@common/menu';
import SEO from '@utils/SEO';

const Section = styled.section`
    background-color: var(--alt-color);
    padding: 2.5rem 0;
    overflow: hidden;

    @media (min-width: ${props => props.theme.screen.md}) {
        padding: 5rem 0;
    }
`;

const Grid = styled.div`
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: min-content;
    grid-gap: 0;
    justify-content: space-between;
    border-top: 1px solid var(--border-color);

    @media (min-width: ${props => props.theme.screen.sm}) {
        grid-template-columns: repeat(2, 1fr);
    }
    @media (min-width: ${props => props.theme.screen.lg}) {
        grid-template-columns: repeat(3, 1fr);
    }
`;

const Box = styled.div`
    padding: 2.5rem 1.5rem 1rem;
    border: 1px solid var(--border-color);
    border-top: 0;
    border-left: 0;
    background-color: white;

    &:hover {
        cursor: pointer;
        background-color: var(--alt-color);
    }
`;

const SectionText = styled.div`
    margin-bottom: 2.5rem;
    padding-left: 1.5rem;
    text-transform: uppercase;
    letter-spacing: 1px;
`;

const Pagination = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 2.5rem;
    margin-left: 1.5rem;
    padding: 0;
`;

export default class Archive extends Component {
    render() {
        const { data, pageContext } = this.props;
        const { edges: posts } = data.allMdx;
        const { previousPagePath, nextPagePath } = pageContext;

        return (
            <Layout>
                <SEO title="BLOG" />
                <Menu fluid />
                <Header
                    title="Goodbye, WordPress. Hello, Gatsby."
                    description="Gatsby.js is one of the most exciting technologies happened in this decade. Building lightning fast websites have never been easier before."
                    headerImage="tony.png"
                />
                <Section>
                    <SectionText>
                        <h3>Recent Posts</h3>
                    </SectionText>
                    <Grid>
                        {posts.map(({ node }, i) => (
                            <Box>
                                <Link to={node.fields.slug} key={i}>
                                    <h2>{node.frontmatter.title}</h2>
                                    <p style={{ fontSize: `90%` }}>
                                        {node.excerpt}
                                    </p>
                                    <p className="label">
                                        {node.frontmatter.date} •{' '}
                                        {node.timeToRead} Min Read
                                    </p>
                                </Link>
                            </Box>
                        ))}
                        <Pagination>
                            {nextPagePath ? (
                                <Link to={nextPagePath}>
                                    &larr; Older Posts
                                </Link>
                            ) : null}
                            {previousPagePath ? (
                                <Link to={previousPagePath}>
                                    Newer Posts &rarr;
                                </Link>
                            ) : null}
                        </Pagination>
                    </Grid>
                </Section>
                <Footer fluid />
            </Layout>
        );
    }
}

export const listQuery = graphql`
    query($skip: Int, $limit: Int) {
        allMdx(
            filter: { fileAbsolutePath: { regex: "/posts/" } }
            sort: { order: DESC, fields: [frontmatter___date] }
            skip: $skip
            limit: $limit
        ) {
            edges {
                node {
                    id
                    excerpt(pruneLength: 250)
                    fields {
                        slug
                    }
                    frontmatter {
                        date(fromNow: true)
                        title
                    }
                    timeToRead
                }
            }
        }
    }
`;
