import React from 'react';
import { useStaticQuery, graphql, Link } from 'gatsby';
import Fade from 'react-reveal/Fade';
import BackgroundImage from 'gatsby-background-image';
import { Container } from '@styles/global';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import styled from 'styled-components';

const HeaderWrapper = styled(Container)`
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 1;

    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
`;

const Overlay = styled.div`
    background: rgba(12, 23, 41, 0.8);
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    opacity: 1;
    transition: all 0.4s ease-in-out 0s;
`;

const HeaderText = styled.h1`
    color: var(--accent-color);
    font-size: 2rem;
    font-weight: 900;
    letter-spacing: 2px;
    text-transform: uppercase;

    @media (min-width: ${props => props.theme.screen.sm}) {
        font-size: 3rem;
    }

    @media (min-width: ${props => props.theme.screen.lg}) {
        font-size: 4rem;
    }

    span {
        color: var(--yellow);
        font-size: 2.5rem;
        @media (min-width: ${props => props.theme.screen.sm}) {
            font-size: 4rem;
        }
        @media (min-width: ${props => props.theme.screen.md}) {
            font-size: 5rem;
        }
    }
`;

const Text = styled.h2`
    color: var(--yellow);
    margin-bottom: 0;
    font-weight: 400;
`;

export default function Header({ fileName }) {
    const data = useStaticQuery(
        graphql`
            query {
                placeholderImage: allFile(
                    filter: { sourceInstanceName: { eq: "art" } }
                ) {
                    edges {
                        node {
                            relativePath
                            childImageSharp {
                                fluid(maxWidth: 2480, quality: 100) {
                                    ...GatsbyImageSharpFluid_withWebp
                                }
                            }
                        }
                    }
                }
            }
        `
    );

    const image = data.placeholderImage.edges.find(
        ({ node }) => node.relativePath === fileName
    ).node;

    if (!image) {
        return null;
    }

    return (
        <>
            <BackgroundImage
                fluid={image.childImageSharp.fluid}
                style={{
                    height: `100vh`,
                    width: `100vw`,
                    backgroundColor: `transparent`,
                    backgroundSize: `cover`,
                    backgroundPosition: `center center`,
                    display: `flex`,
                    alignItems: `center`,
                }}
            >
                <Overlay />
            </BackgroundImage>
            <HeaderWrapper>
                <div>
                    <Fade top>
                        <Text>we help businesses</Text>
                    </Fade>
                    <Fade bottom>
                        <HeaderText>
                            innovate <span>+</span> grow
                        </HeaderText>
                    </Fade>

                    <AnchorLink href="#portfolio">
                        <button className="button">See Our Portfolio</button>
                    </AnchorLink>
                </div>
                <div class="mouse">
                    <div class="scroller"></div>
                </div>
            </HeaderWrapper>
        </>
    );
}
